
import { defineComponent, toRefs, computed } from 'vue';

import { ETHER } from '@/constants/tokenlists';
import useNumbers from '@/composables/useNumbers';
import useTokenApproval from '@/composables/trade/useTokenApproval';
import useVueWeb3 from '@/services/web3/useVueWeb3';
import useTokens from '@/composables/useTokens';

export default defineComponent({
  emits: ['trade', 'close'],
  props: {
    open: {
      type: Boolean,
      default: false
    },
    isV1Swap: {
      type: Boolean,
      required: true
    },
    addressIn: {
      type: String,
      required: true
    },
    amountIn: {
      type: String,
      required: true
    },
    addressOut: {
      type: String,
      required: true
    },
    amountOut: {
      type: String,
      required: true
    },
    trading: {
      type: Boolean,
      required: true
    }
  },
  setup(props, { emit }) {
    const { fNum, toFiat } = useNumbers();

    const { addressIn, amountIn, addressOut, isV1Swap } = toRefs(props);

    const { tokens } = useTokens();
    const { userNetworkConfig } = useVueWeb3();

    const isWrap = computed(() => {
      const config = userNetworkConfig.value;
      return (
        addressIn.value === ETHER.address &&
        addressOut.value === config.addresses.weth
      );
    });

    const isUnwrap = computed(() => {
      const config = userNetworkConfig.value;
      return (
        addressOut.value === ETHER.address &&
        addressIn.value === config.addresses.weth
      );
    });

    const {
      approving,
      approveV1,
      approveV2,
      allowanceState
    } = useTokenApproval(addressIn, amountIn, tokens);

    const valueIn = computed(() => toFiat(amountIn.value, addressIn.value));

    const symbolIn = computed(() => {
      const token = tokens.value[addressIn.value];
      if (!token) {
        return '';
      }
      return token.symbol;
    });

    const symbolOut = computed(() => {
      const token = tokens.value[addressOut.value];
      if (!token) {
        return '';
      }
      return token.symbol;
    });

    const isEthTrade = computed(() => addressIn.value === ETHER.address);

    const requiresApproval = computed(() => {
      if (isWrap.value || isUnwrap.value || isEthTrade.value) return false;
      return true;
    });

    const isApproved = computed(() => {
      return isV1Swap.value
        ? allowanceState.value.isUnlockedV1
        : allowanceState.value.isUnlockedV2;
    });

    async function approve(): Promise<void> {
      if (isV1Swap.value) {
        await approveV1();
      } else {
        await approveV2();
      }
    }

    function trade() {
      emit('trade');
    }

    function onClose() {
      emit('close');
    }

    return {
      fNum,
      requiresApproval,
      isApproved,
      valueIn,
      symbolIn,
      symbolOut,
      onClose,
      approve,
      approving,
      trade
    };
  }
});
